  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  margin: 100px 0 20px 100px;
  width: 500px;
  height: 300px;
}
.hide, .riverflow-app,
.earth-mtn, .data-close {
  display: none;
  visibility: hidden;
}
.show {
  display: block;
  visibility: visible;
}
.transparent {
  pointer-events: none;
  opacity: 0;
}
.opaque {
  opacity: 1;
}
.inline-svg {
  display: inline-block;
  width: 100%;
}
.psuedo-element {
  content: '';
  display: block;
  height: 100%;
  position: absolute;
  width: 100%;
}
