/* Prevents img without src to appear */
img:not([src]) {
  visibility: hidden;
}
::-webkit-scrollbar {
  width: 0.7em;
}

/* Track */
::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: rgba(0, 0, 0, 0.2);
}
::-webkit-scrollbar-thumb:window-inactive {
  background: rgba(0, 0, 0, 0.4);
}
html, body, #app {
  position: absolute;
  width: 100%;
  height: 100%;
  min-height: 100%;
  top: 0;
  left: 0;
}
body {
  font-family: 'Open Sans Condensed', helvetica, sans-serif;
  margin: 0;
  -webkit-transition: background-color 1.3s;
  transition: background-color 1.3s;
}
a:link {
  color: #177fad;
}
a:visited {
  color: #115e80;
}
a:hover {
  color: #1da0da;
}
.logo, h1, h2, h3, h4 {
  color: rgba(0, 0, 0, 0.8);
  font-family: 'Amatic SC', serif;
  font-weight: 700;
}
h3 {
  font-size: 160%;
  margin: 0 0 10px;
}
.container {
  max-height: 85%;
  margin: 0 auto;
}
.starfield-wrapper {
  visibility: hidden;
}
.logo {
  color: rgba(0, 0, 0, 0.5);
  font-size: 170%;
  margin: 0;
  position: absolute;
  top: calc(20px / 2);
  left: 20px;
}
.title {
  display: none;
  left: 0;
  margin: 0;
  position: absolute;
  right: 0;
  text-align: center;
}
@media (min-width: 500px) {
.title {
      display: block;
      -webkit-transform: translateY(10px);
              transform: translateY(10px);
      top: 0;
}
}
.title-nav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  position: absolute;
  top: 40px;
  right: 20px;
  width: 140px;
  z-index: 2;
}
.nav-arrow {
  display: block;
  width: 56px;
  height: 49px;
  position: relative;
  -webkit-transition: 0.25s;
  transition: 0.25s;
}
.nav-right:hover {
  -webkit-transform: translateX(5px);
          transform: translateX(5px);
}
.nav-left:hover {
  -webkit-transform: translateX(-5px);
          transform: translateX(-5px);
}
.nav-earth {
  background: rgba(0, 0, 0, 0.2);
  width: 27px;
  height: 18px;
  -webkit-transform: translateY(15px);
          transform: translateY(15px);
  -webkit-transition: all .25s;
  transition: all .25s;
}
.nav-earth:hover {
    background: rgba(0, 0, 0, 0.4);
}
.arrow-right {
  -webkit-transform: scale(-1, 1);
          transform: scale(-1, 1);
}
#arrow-top,
#arrow-bottom {
  fill: #000;
  opacity: 0.3;
}
#arrow-bottom {
  opacity: 0.4;
}
.data {
  background-color: rgba(255, 255, 255, 0.2);
  display: inline-block;
  max-height: 400px;
  overflow-y: auto;
  font-size: 130%;
  padding: 20px;
  width: 100%;
  z-index: 5;
}
@media (min-width: 500px) {
.data {
      font-size: 100%;
      position: absolute;
      right: 20px;
      top: 100px;
      -webkit-transition: 1.3s cubic-bezier(0.785, 0.135, 0.15, 0.86);
      transition: 1.3s cubic-bezier(0.785, 0.135, 0.15, 0.86);
      max-width: 25vw;
}
}
.data p {
    margin: 10px 0 0;
}
.data p:first-of-type {
      margin-top: 0;
}
.data-close {
  cursor: pointer;
  font: 18px/100% arial, sans-serif;
  width: 15px;
  height: 15px;
  position: absolute;
  top: 10px;
  right: 10px;
}
.data-close:after {
    content: '✖';
    /* UTF-8 symbol */
    color: rgba(45, 48, 68, 0.3);
    -webkit-transition: all 0.25s;
    transition: all 0.25s;
}
.data-close:hover:after {
    color: #2D3044;
    font-size: 20px;
}
@media (min-width: 500px) {
.data-close {
      display: block;
}
}
.data-title {
  margin: 0 0 20px;
}
@media (min-width: 500px) {
.data-title {
      display: none;
      visibility: hidden;
}
}
.data-elevation b {
  font-weight: inherit;
  white-space: nowrap;
}
