  position: absolute;
  left: 20px;
  -webkit-transform: translateY(-61px);
          transform: translateY(-61px);
}
.svg-camping .svg-part {
    fill: #000;
    opacity: 0.08;
    -webkit-transform-origin: 50% 100%;
            transform-origin: 50% 100%;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}
.svg-camping:hover .svg-tent {
    -webkit-animation: sway 3.8s infinite;
            animation: sway 3.8s infinite;
}
.svg-camping:hover .svg-tree-1 {
    -webkit-animation: sway 3.2s 0.2s infinite;
            animation: sway 3.2s 0.2s infinite;
}
.svg-camping:hover .svg-tree-2 {
    -webkit-animation: sway 3.2s 0.3s infinite;
            animation: sway 3.2s 0.3s infinite;
}
.svg-camping:hover .svg-tree-3 {
    -webkit-animation: sway 3.2s 0.6s infinite;
            animation: sway 3.2s 0.6s infinite;
}
@-webkit-keyframes sway {
0% {
    -webkit-transform: skewX(0);
            transform: skewX(0);
}
30% {
    -webkit-transform: skewX(9deg);
            transform: skewX(9deg);
}
80% {
    -webkit-transform: skewX(-8deg);
            transform: skewX(-8deg);
}
100% {
    -webkit-transform: skewX(0);
            transform: skewX(0);
}
}
@keyframes sway {
0% {
    -webkit-transform: skewX(0);
            transform: skewX(0);
}
30% {
    -webkit-transform: skewX(9deg);
            transform: skewX(9deg);
}
80% {
    -webkit-transform: skewX(-8deg);
            transform: skewX(-8deg);
}
100% {
